form {
    padding-top: 50px;
}

input[type="text"] {
    /* background: #343A40; */
    border: 1px solid #2E3238;
    border-radius: 8px;
    padding: 7px 20px 7px 35px;
    color: #6e7373;
    width: 90%;
}

input[type="text"]:focus {
    outline: none;
}

.input-icon i {
    position: absolute;
}

.input-icon {
    width: 100%;
    height: 100%;
    position: relative;
}

.icon {
    padding: 15px;
    color: #6e7373;
    top: 50%;
    transform: translateY(-50%);
    padding-left: 10px;
}